/**
 * Callable class
 */
export default class Callable extends Function {
  constructor () {
    super()
    if (!Object.prototype.hasOwnProperty.call(this, '__call')) {
      throw new TypeError('Class mast be implement method "__call"')
    }

    return new Proxy(this, {
      apply: (target, thisArg, args) => target.__call(...args)
    })
  }
}
